import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
// creates a function that invokes the given functions from right to left.
//  import flowRight from 'lodash/flowRight';

import SwipeableViews from 'react-swipeable-views';
import Box from '@mui/material/Box';
import { autoPlay, virtualize, bindKeyboard } from 'react-swipeable-views-utils';

const EnhancedSwipeableViews = bindKeyboard(autoPlay(virtualize(SwipeableViews)));

export default function MemoryImageSlide({ index, images, handleNext }) {
  const [activeStep, setActiveStep] = useState(index);
  const [loadedImages, setLoadedImages] = useState(0);
  const [display, setDisplay] = useState(null);

  useEffect(() => {
    setActiveStep(index);
  }, [index]);

  useEffect(() => {
    if (loadedImages < images.length) {
      setDisplay('none');
    } else {
      setDisplay('flex');
    }
  }, [loadedImages]);

  const handleStepChange = (step) => {
    setActiveStep(step);
  };

  const handleImageLoad = () => setLoadedImages((prevLoaded) => prevLoaded + 1);

  const imgElements = images.map((image) => {
    const srcSetList = image.images ? image.images.map(({ src, width }) => `${src} ${width}w`) : [];
    const srcSet = srcSetList.join(',');
    const style = {
      display: 'block',
      boxSizing: 'content-box',
      cursor: 'pointer',
      objectFit: 'contain',
      height: '100vh',
    };

    return (
      <Box
        component="img"
        onLoad={handleImageLoad}
        sx={{
          display: { display },
          justifyContents: 'center',
          width: '100%',
          bgcolor: '#1c1c1c',
        }}
        src={image.src}
        srcSet={srcSet}
        style={style}
        alt={image.src}
        key={image.src}
      />
    );
  });

  return (
    <EnhancedSwipeableViews
      index={activeStep}
      onChangeIndex={handleStepChange}
      enableMouseEvents
      animateTransitions
      interval={10000}
      onClick={handleNext}
      style={{ backgroundColor: 'muted.main' }}
    >
      {imgElements}
    </EnhancedSwipeableViews>
  );
}

MemoryImageSlide.defaultProps = {
  index: 0,
};

MemoryImageSlide.propTypes = {
  index: PropTypes.number,
  handleNext: PropTypes.func.isRequired,
  images: PropTypes.arrayOf(PropTypes.shape({
    src: PropTypes.string,
    width: PropTypes.number,
    height: PropTypes.number,
  })).isRequired,
};
